.city-item {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  line-height: 1.5;
}

.city-item img {
  height: 80px;
  margin-right: 25px;
}

.wrapper {
  display: flex;
  flex-direction: column;
}
.title {
  font-weight: bold;
}

.green {
  color: #57bd84;
}

.weather {
  color: black;
  text-transform: lowercase;
}

.temp-container .temp {
  display: inline-block;
  min-width: 10px;
  padding: 2px 7px;
  font-size: 12px;
  font-weight: 700;
  color: #ffffff;
  line-height: 1;
  vertical-align: baseline;
  white-space: nowrap;
  text-align: center;
  background-color: #4e4d4a;
  border-radius: 10px;
}

.coords {
  cursor: pointer;
}
